<template>
    <div id="chart" style="width:100%;height:350px;"></div>
</template>

<script>
    export default {
        name: "echart",
        props:['datas'],
        mounted() {
            this.drawChart()
        },
        methods:{
            drawChart() {
                let x = [], search_his=[],clicks_his=[],convers_his=[];
                this.datas.searchs_his.forEach(item=>{
                   x.push(item.split(',')[0])
                });
                this.datas.searchs_his.forEach(item=>{
                    search_his.push(item.split(',')[1])
                });
                this.datas.clicks_his.forEach(item=>{
                    clicks_his.push(item.split(',')[1])
                });
                this.datas.convers_his.forEach(item=>{
                    convers_his.push(item.split(',')[1])
                });

                let myChart = echarts.init(document.getElementById("chart"));
                // 指定图表的配置项和数据
                let option = {
                    color: ['#add1f8','#ffc015','#0c6'],
                    tooltip: {
                        trigger: 'axis',
                        axisPointer: {
                            type: 'cross',
                            crossStyle: {
                                color: '#999'
                            },
                            label: {
                                backgroundColor: '#ccc',
                                color: '#222'
                            }
                        },
                        formatter: function (params) {
                            let result = '';
                            params.forEach(function (item, index) {
                                result += item.marker + " " + item.seriesName + " : " + (index>0?item.value+'%':item.value) +"</br>";
                            });
                            return result;
                        }
                    },
                    legend: {
                        data: ["搜索量","点击率","转化率"]
                    },
                    xAxis: {
                        data: x,
                        splitLine: {
                            show: false
                        }
                    },
                    yAxis: [{
                        splitLine: {
                            show: false
                        }
                    },{
                        splitLine: {
                            show: false
                        }
                    }],
                    series: [
                        {
                            name: "搜索量",
                            type: "bar",
                            barWidth:30,
                            data: search_his,
                        },
                        {
                            name: "点击率",
                            type: "line",
                            yAxisIndex: 1,
                            data: clicks_his,
                        },
                        {
                            name: "转化率",
                            type: "line",
                            yAxisIndex: 1,
                            data: convers_his
                        }
                    ]
                };
                // 使用刚指定的配置项和数据显示图表。
                myChart.setOption(option);
            }
        }
    }
</script>
