<template>
    <div class="wrap" v-bind:class="{ init: !showResult }">
        <div class="header">
            <h1 class="logo"><img src="~@/assets/logo.png" alt=""></h1>
            <div class="links">
                <a href="#" class="link-home">首页</a>
                <a href="#" class="link-report">关键词报告</a>
                <a href="#" class="link-login">登录</a>
            </div>
        </div>
        <div class="search">
            <div class="search-in">
                <div class="col-select">
                    <a-select defaultValue="country" v-model="country" class="select" size="large">
                        <a-select-option v-for="d in countrys" :key="d.value"><i :style="{backgroundImage:`url(${d.img})`}" class="flag"></i>{{d.name}}</a-select-option>
                    </a-select>
                </div>
                <div class="col-input">
                    <a-auto-complete class="input" size="large"
                                     v-model="keyword"
                                     :dataSource="dataSource"
                                     placeholder="请输入关键词"
                                     :defaultActiveFirstOption="false"
                                     :allowClear="true"
                                     @search="onSearchKeyword"
                                     @keyup.enter.native="onSearch"
                    />
                </div>
                <div class="col-btn">
                    <a-button html-type="submit" class="btn" size="large" @click="onSearch">搜索</a-button>
                </div>
            </div>
        </div>
        <div class="result" v-if="showResult">
            <div class="total">找到<b>{{ counts }}</b>条记录</div>
            <a-table :columns="tableColumns" :loading="loading" :rowKey="record => record.keyword"
                     :dataSource="tableDataSource" :pagination="pagination">
                <template v-slot:keyword="text, row">
                    <span @click="() => {
                          onShowDetail(row.keyword);
                    }" class="keyword">{{text}}</span>
                    <a-tooltip placement="topLeft" arrowPointAtCenter title="去亚马逊搜索此关键词">
                        <a :href="`${currentCountryInfo.link}?k=${row.keyword}`" target="_blank" class="link-amazon"></a>
                    </a-tooltip>
                </template>
                <template v-slot:numFormat="text, row">
                    <div class="right">
                        <span v-if="text<0" class="gray center">N/A</span>
                        <span v-else>{{text|number}}</span>
                    </div>
                </template>
                <template v-slot:precentFormat="text, row">
                    <div class="right">{{text}}%</div>
                </template>
                <template v-slot:stringFormat="text, row">
                    <span v-if="text.length<1" class="gray">N/A</span>
                    <span v-else>{{text}}</span>
                </template>

                <template v-for="h in tableColumns">
                    <span :slot="`customTitle_${h.dataIndex}`">
                        <span v-if="h.desc.length>0">
                            <a-tooltip>
                                <template slot="title">
                                  {{h.desc}}
                                </template>
                                <span class="table-tit">{{h.name}}</span>
                            </a-tooltip>
                        </span>
                        <span v-else>{{h.name}}</span>
                    </span>
                </template>
            </a-table>
        </div>
        <a-drawer class="chart"
                placement="bottom"
                :closable="false"
                @close="onCloseDrawer"
                :destroyOnClose="true"
                :visible="visible"
                  height="500px"
        >
            <div class="chart-tit">关键词：<strong class="key">{{ detailKeyword }}</strong></div>
            <div class="chart-datas">
                <div class="item">月份：{{detailDataSource.month}}</div>
                <div class="item">top3点击率：{{detailDataSource.top3Clicks}}%</div>
                <div class="item">top3转化率：{{detailDataSource.top3Convers}}%</div>
                <div class="item">搜索量：<span v-if="detailDataSource.searchs<0">N/A</span>
                    <span v-else>{{detailDataSource.searchs|number}}</span></div>
                <div class="item">去年同期搜索量：<span v-if="detailDataSource.yoySearchs<0">N/A</span>
                    <span v-else>{{detailDataSource.yoySearchs|number}}</span></div>
                <div class="item">上月搜索量：<span v-if="detailDataSource.momSearchs<0">N/A</span>
                    <span v-else>{{detailDataSource.momSearchs|number}}</span></div>
            </div>
            <echart :datas="detailDataSource"></echart>
        </a-drawer>
    </div>
</template>

<script>
    import Select from 'ant-design-vue/lib/select';
    import 'ant-design-vue/lib/select/style/index.less';
    import AutoComplete from 'ant-design-vue/lib/auto-complete';
    import Button from 'ant-design-vue/lib/button';
    import 'ant-design-vue/lib/button/style/index.less';
    import Table from 'ant-design-vue/lib/table';
    import 'ant-design-vue/lib/table/style/index.less';
    import Drawer from 'ant-design-vue/lib/drawer';
    import 'ant-design-vue/lib/drawer/style/index.less';
    import Tooltip from 'ant-design-vue/lib/tooltip';
    import 'ant-design-vue/lib/tooltip/style/index.less';
    import axios from 'axios';
    import numeral from  'numeral';
    import Echart from "./echart";

    axios.interceptors.response.use(response => {
        // IE 9
        if (response.data == null && response.config.responseType === 'JSON' && response.request.responseText != null) {
            try {
                response.data = JSON.parse(response.request.responseText);
            } catch (e) {
            }
        }
        return response;
    });

    const countrys = [{
        name:'美国',
        value: 'us',
        link: 'https://www.amazon.com/s',
        img: require('@/assets/us.svg')
    }, {
        name: '加拿大',
        value: 'ca',
        link:'https://www.amazon.ca/s',
        img: require('@/assets/ca.svg')
    }, 
    // {
    //     name:'德国',
    //     value: 'de',
    //     img: require('@/assets/de.svg')
    // },{
    //     name:'西班牙',
    //     value: 'es',
    //     img: require('@/assets/es.svg')
    // },{
    //     name:'法国',
    //     value: 'fr',
    //     img: require('@/assets/fr.svg')
    // },{
    //     name:'印度',
    //     value: 'in',
    //     img: require('@/assets/in.svg')
    // },{
    //     name:'意大利',
    //     value: 'it',
    //     img: require('@/assets/it.svg')
    // },{
    //     name:'日本',
    //     value: 'jp',
    //     img: require('@/assets/jp.svg')
    // },{
    //     name:'英国',
    //     value: 'uk',
    //     img: require('@/assets/uk.svg')
    // }
    ]

    const tableColumns = [{
        name: "关键词",
        desc: '',
        slots: { title: 'customTitle_keyword' },
        dataIndex: 'keyword',
        scopedSlots: {customRender: 'keyword'}
    }, {
        name: "月份",
        desc: '',
        slots: { title: 'customTitle_month' },
        dataIndex: 'month',
    }, {
        name: "月搜索量",
        desc: '',
        slots: { title: 'customTitle_searchs' },
        dataIndex: 'searchs',
        scopedSlots: {customRender: 'numFormat'}
    }, {
        name: "去年同月",
        desc: '',
        slots: { title: 'customTitle_yoySearchs' },
        dataIndex: 'yoySearchs',
        scopedSlots: {customRender: 'numFormat'}
    }, {
        name: "上月搜索量",
        desc: '',
        slots: { title: 'customTitle_momSearchs' },
        dataIndex: 'momSearchs',
        scopedSlots: {customRender: 'numFormat'}
    }, {
        name: "Top3点击占比",
        desc: 'Top3的商品点击量占该关键词总点击量的比率',
        slots: { title: 'customTitle_top3clicks' },
        dataIndex: 'top3clicks',
        scopedSlots: {customRender: 'precentFormat'}
    }, {
        name: "Top3转化占比",
        desc: 'Top3的商品转化数占该关键词总转化数的比率',
        slots: { title: 'customTitle_top3convers' },
        dataIndex: 'top3convers',
        scopedSlots: {customRender: 'precentFormat'}
    }, {
        name: "近3月霸榜率",
        desc: '近3个月，该关键词下，Top3中出现过的商品与Top3中总的商品数的比率',
        slots: { title: 'customTitle_month3Asins' },
        dataIndex: 'month3Asins',
        scopedSlots: {customRender: 'stringFormat'}
    }, {
        name: "近6月霸榜率",
        desc: '近6个月，该关键词下，Top3中出现过的商品与Top3中总的商品数的比率',
        slots: { title: 'customTitle_month6Asins' },
        dataIndex: 'month6Asins',
        scopedSlots: {customRender: 'stringFormat'}
    }, {
        name: "近12月霸榜率",
        desc: '近12个月，该关键词下，Top3中出现过的商品与Top3中总的商品数的比率',
        slots: { title: 'customTitle_month12Asins' },
        dataIndex: 'month12Asins',
        scopedSlots: {customRender: 'stringFormat'}
    }];

    export default {
        components: {
            [Select.name]: Select,
            [Select.Option.name]: Select.Option,
            [AutoComplete.name]: AutoComplete,
            [Table.name]: Table,
            [Button.name]: Button,
            [Drawer.name]: Drawer,
            [Tooltip.name]: Tooltip,
            echart: Echart,
        },
        filters:{
            number(val){
                return numeral(val).format('0,0')
            }
        },
        data() {
            return {
                countrys,
                dataSource :[],
                tableColumns,
                tableDataSource: [],
                detailDataSource: [],
                showResult: false,
                loading: true,
                pagination: false,
                visible: false,
                country: 'us',
                keyword: '',
                detailKeyword:'',
                counts: 0,
                isNextLoaded: true,
                currentCountryInfo:{}
            };
        },
        mounted() {
            window.addEventListener('scroll', this.scroll)
        },
        destroyed() {
            window.removeEventListener('scroll', this.scroll)
        },
        methods: {
            scroll() {
                if(this.isNextLoaded && this.tableDataSource.length>0){
                    let bottomOfWindow = (document.documentElement.scrollTop + window.innerHeight + 10) >= document.documentElement.offsetHeight;

                    if (bottomOfWindow) {
                        this.isNextLoaded = false;
                        this.onNextPage()
                    }
                }
            },
            onSearchKeyword(searchText) {
                if (searchText.length >= 3) {
                    // axios.get(`/js/autocomplete.json`, {
                    axios.get(`/api/autocomplete`, {
                        params: {
                            country: this.country,
                            keyword: searchText
                        }
                    })
                        .then(response => {
                            if(response.data.code>0){
                                this.dataSource = response.data.data.suggests;
                            }else {
                                alert(response.data.msg)
                            }
                        })
                        .catch(error => {
                            console.log(error);
                        });
                }
            },
            onSearch() {
                if (!this.keyword ||!this.keyword.length) {
                    alert('请输入关键词')
                    return
                }
                // axios.get(`/js/search.json`, {
                axios.get(`/api/search`, {
                    params: {
                        country: this.country,
                        keyword: this.keyword
                    }
                })
                    .then(response => {
                        if(response.data.code>0){
                            this.showResult = true;
                            this.dataSource = [];
                            this.currentCountryInfo = this.countrys.find(item=>{
                                return item.value === this.country;
                            })
                            this.tableDataSource = response.data.data.keywords;
                            this.counts = response.data.data.count;
                        }else {
                            alert(response.data.msg)
                        }
                    })
                    .catch(error => {
                        console.log(error);
                    });
            },
            onNextPage() {
                // axios.get(`/js/nextpage.json`, {
                axios.get(`/api/nextpage`, {
                    params: {
                        country: this.country,
                        keyword: this.keyword
                    }
                })
                    .then(response => {
                        if(response.data.code>0){
                            this.isNextLoaded = true
                            this.tableDataSource.push( ...(response.data.data.keywords));
                        }else {
                            alert(response.data.msg)
                        }
                    })
                    .catch(error => {
                        console.log(error);
                    });
            },
            onShowDetail(key) {
                this.detailKeyword = key;
                // axios.get(`/js/detail.json`, {
                axios.get(`/api/detail`, {
                    params: {
                        country: this.country,
                        keyword: key
                    }
                })
                    .then(response => {
                        if(response.data.code>0){
                            this.detailDataSource = response.data.data
                            this.showDrawer()
                        }else {
                            alert(response.data.msg)
                        }
                    })
                    .catch(error => {
                        console.log(error);
                    });
            },
            onCloseDrawer(){
                this.visible = false;
            },
            showDrawer() {
                this.visible = true;
            }
        },
    };
</script>
